const emailValidator = (email: string) => {
  const emailRegex = new RegExp(
    /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/g,
  );
  return emailRegex.test(email) && email.length !== 0;
};

const passwordValidator = (password: string) => {
  const hasDigit = new RegExp(/\d/).test(password);
  const hasUpperCase = new RegExp(/[A-Z]/).test(password);
  const hasLowerCase = new RegExp(/[a-z]/).test(password);
  const hasSpecialCharacter = new RegExp(
    /[!@#$%^&*()_+\-=[\]{};':'\\|,.<>?]/,
  ).test(password);
  return { hasDigit, hasUpperCase, hasLowerCase, hasSpecialCharacter };
};

export { emailValidator, passwordValidator };
