import axios from 'axios';

const register = async (formData: FormData) => {
  try {
    const response = await axios.post('/api/user/create', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    });
    return response;
  } catch (error) {
    throw error;
  }
};

export { register };
