import {
  BarChart as MuiBarChart,
  LineChart as MuiLineChart,
  PieChart as MuiPieChart,
} from '@mui/x-charts';
import { months } from '../config/constants';

export const PieChart = () => {
  return (
    <MuiPieChart
      series={[
        {
          data: [
            { id: 0, value: 10, label: 'images' },
            { id: 1, value: 15, label: 'documents' },
            { id: 2, value: 20, label: 'videos' },
            { id: 3, value: 12, label: 'others' },
          ],
        },
      ]}
      height={300}
    />
  );
};

export const BarChart = () => {
  return (
    <MuiBarChart
      title="Items uploaded"
      xAxis={[
        {
          data: months,
          scaleType: 'band',
        },
      ]}
      series={[
        {
          label: 'Items uploaded',
          data: [2, 5, 3, 9, 2, 7, 4, 6, 8, 1, 10, 13],
        },
      ]}
      height={300}
    />
  );
};

export const LineChart = () => {
  return (
    <MuiLineChart
      xAxis={[{ data: [1, 2, 3, 5, 8, 10] }]}
      series={[
        {
          data: [2, 5.5, 2, 8.5, 1.5, 5],
          area: true,
        },
      ]}
      height={300}
    />
  );
};
