import { Alert, Box, Snackbar } from '@mui/material';
import { useState } from 'react';
import SignInComponent from '../components/SignIn';
import { signin } from '../utils/api/helpers/signin';
import { emailValidator } from '../utils/validator';

const SignInPage = () => {
  const [dto, setDto] = useState<{ email: string; password: string }>({
    email: '',
    password: '',
  });
  const [validationError, setValidationError] = useState<{
    email: boolean;
  }>({ email: false });
  const [submissionResponse, setSubmissionResponse] = useState<{
    error: boolean;
    success: boolean;
    loading: boolean;
  }>({ error: false, success: false, loading: false });

  /* signin */
  const handleSignIn = () => {
    const isValidEmail = emailValidator(dto.email);
    if (isValidEmail) {
      setSubmissionResponse({ ...submissionResponse, loading: true });
      signin(dto)
        .then((response) => {
          localStorage.setItem('token', response.data.token);
          setSubmissionResponse({
            ...submissionResponse,
            success: true,
            loading: false,
          });
        })
        .catch((error) => {
          setSubmissionResponse({
            ...submissionResponse,
            error: true,
            loading: false,
          });
        });
    } else {
      setValidationError({
        ...validationError,
        email: !isValidEmail,
      });
    }
  };

  /* handle change */
  const handleDtoChange = (type: 'email' | 'password', value: string) => {
    switch (type) {
      case 'email':
        if (validationError.email && emailValidator(value)) {
          setValidationError({ ...validationError, email: false });
        }
        break;
    }
    setDto({ ...dto, [type]: value });
  };

  /* handle submission */
  const handleSubmissionClose = (type: 'error' | 'success') => {
    setSubmissionResponse({ ...submissionResponse, [type]: false });
  };

  return (
    <Box
      height="100vh"
      display="flex"
      justifyContent="center"
      alignItems="center"
    >
      <Snackbar
        open={submissionResponse.error}
        autoHideDuration={3000}
        onClose={() => handleSubmissionClose('error')}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      >
        <Alert variant="outlined" severity="error">
          Could not Sign In
        </Alert>
      </Snackbar>
      <Snackbar
        open={submissionResponse.success}
        autoHideDuration={3000}
        onClose={() => handleSubmissionClose('success')}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      >
        <Alert variant="outlined" severity="success">
          Signed In Successfully
        </Alert>
      </Snackbar>
      <SignInComponent
        dto={dto}
        loading={submissionResponse.loading}
        validationError={validationError}
        handleClick={handleSignIn}
        handleDtoChange={handleDtoChange}
      />
    </Box>
  );
};

export default SignInPage;
