import axios from 'axios';

const signin = async (dto: { email: string; password: string }) => {
  try {
    const response = await axios.post('/api/auth/signin', dto, {
      headers: {
        'Content-Type': 'application/json',
      },
    });
    return response;
  } catch (error) {
    throw error;
  }
};

export { signin };
