import { VisibilityOffOutlined, VisibilityOutlined } from '@mui/icons-material';
import { LoadingButton } from '@mui/lab';
import {
  Button,
  Card,
  CardContent,
  CardHeader,
  CircularProgress,
  Container,
  Divider,
  Unstable_Grid2 as Grid,
  IconButton,
  InputAdornment,
  TextField,
  Typography,
} from '@mui/material';
import { FC, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { IRegisterProps } from '../types/IRegisterProps';
import ImageInput from './ImageInput';

const Register: FC<IRegisterProps> = ({
  dto,
  validationError,
  handleDtoChange,
  handleClick,
  loading,
}) => {
  const [isPasswordVisible, setIsPasswordVisible] = useState(false);
  const [image, setImage] = useState<string>();
  const navigate = useNavigate();
  return (
    <Container maxWidth="md">
      <Card variant="outlined" sx={{ p: 4 }}>
        <CardHeader
          title="Register"
          titleTypographyProps={{ variant: 'h4', component: 'span' }}
        />
        <CardContent component={Grid} container spacing={3}>
          <Grid width="100%" display="flex" justifyContent="center">
            <ImageInput
              id="registration-image"
              src={image}
              onChange={(event) => {
                const file = event.target.files?.item(0) as Blob;
                const fileReader = new FileReader();
                fileReader.onload = () => {
                  setImage(fileReader.result as string);
                };
                fileReader.readAsDataURL(file);
                handleDtoChange('image', event.target.files?.item(0)!);
              }}
            />
          </Grid>
          <Grid width="100%">
            <TextField
              id="registration-name"
              name="name"
              label="Full Name"
              type="text"
              required
              error={validationError.name}
              helperText={
                validationError.name
                  ? 'Name cannot be less than 2 characters'
                  : null
              }
              value={dto.name}
              fullWidth
              onChange={(event) => {
                handleDtoChange('name', event.target.value);
              }}
            />
          </Grid>
          <Grid width="100%">
            <TextField
              id="registration-email"
              name="email"
              label="Email"
              type="email"
              value={dto.email}
              error={validationError.email}
              helperText={validationError.email ? 'Should be an email' : null}
              required
              fullWidth
              onChange={(event) => {
                handleDtoChange('email', event.target.value);
              }}
            />
          </Grid>
          <Grid width="100%">
            <TextField
              id="registration-password"
              name="password"
              label="Password"
              type={isPasswordVisible ? 'text' : 'password'}
              value={dto.password}
              error={validationError.password}
              helperText={
                validationError.password
                  ? 'Password must have atleast 1 uppercase, 1 lowercase, 1 digit and 1 special character'
                  : null
              }
              required
              fullWidth
              onChange={(event) => {
                handleDtoChange('password', event.target.value);
              }}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton
                      onClick={() => {
                        setIsPasswordVisible(!isPasswordVisible);
                      }}
                      color={validationError.password ? 'error' : 'default'}
                    >
                      {!isPasswordVisible ? (
                        <VisibilityOffOutlined />
                      ) : (
                        <VisibilityOutlined />
                      )}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
            />
          </Grid>
          <Grid width="100%" marginBottom={4}>
            <LoadingButton
              size="large"
              variant="outlined"
              loading={loading}
              loadingIndicator={
                <CircularProgress
                  role="progressbar"
                  color="primary"
                  size={20}
                />
              }
              onClick={(event) => {
                event.preventDefault();
                handleClick();
              }}
            >
              Register
            </LoadingButton>
          </Grid>
          <Grid width="100%">
            <Divider>
              <Typography>Already have an account?</Typography>
            </Divider>
          </Grid>
          <Grid width="100%" display="flex" justifyContent="center">
            <Button
              size="large"
              variant="outlined"
              color="secondary"
              onClick={() => {
                navigate('/signin');
              }}
            >
              Sign In
            </Button>
          </Grid>
        </CardContent>
      </Card>
    </Container>
  );
};

export default Register;
