import { Alert, Box, Snackbar } from '@mui/material';
import { useState } from 'react';
import RegisterComponent from '../components/Register';
import { register } from '../utils/api/helpers/register';
import { emailValidator, passwordValidator } from '../utils/validator';

const RegisterPage = () => {
  const [dto, setDto] = useState<{
    image?: File;
    name: string;
    email: string;
    password: string;
  }>({
    image: undefined,
    name: '',
    email: '',
    password: '',
  });
  const [validationError, setValidationError] = useState<{
    name: boolean;
    email: boolean;
    password: boolean;
  }>({ name: false, email: false, password: false });
  const [submissionResponse, setSubmissionResponse] = useState<{
    error: boolean;
    success: boolean;
    loading: boolean;
  }>({ error: false, success: false, loading: false });

  /* register function */
  const handleRegister = () => {
    let formData = new FormData();
    if (dto.image) {
      formData.append('image', dto.image);
    }
    formData.append('name', dto.name);
    formData.append('email', dto.email);
    formData.append('password', dto.password);
    const isValidName = dto.name.length !== 0;
    const isValidEmail = emailValidator(dto.email);
    const { hasDigit, hasLowerCase, hasUpperCase, hasSpecialCharacter } =
      passwordValidator(dto.password);
    if (
      isValidEmail &&
      hasLowerCase &&
      hasDigit &&
      hasUpperCase &&
      hasSpecialCharacter &&
      isValidName
    ) {
      setSubmissionResponse({ ...submissionResponse, loading: true });
      register(formData)
        .then((response) => {
          setSubmissionResponse({
            ...submissionResponse,
            loading: false,
            success: true,
          });
        })
        .catch((error) => {
          setSubmissionResponse({
            ...submissionResponse,
            loading: false,
            error: true,
          });
        });
    } else {
      setValidationError({
        ...validationError,
        name: !isValidName,
        email: !isValidEmail,
        password: !(
          hasDigit &&
          hasLowerCase &&
          hasUpperCase &&
          hasSpecialCharacter
        ),
      });
    }
  };

  /* change handler */
  const handleDtoChangeRegister = (
    type: 'image' | 'name' | 'email' | 'password',
    value: File | string,
  ) => {
    const passwordValidation =
      type === 'password' && typeof value === 'string'
        ? passwordValidator(value)
        : undefined;
    switch (type) {
      case 'name':
        if (
          validationError.name &&
          typeof value === 'string' &&
          value.length < 1
        ) {
          setValidationError({ ...validationError, name: false });
        }
        break;
      case 'email':
        if (
          validationError.email &&
          typeof value === 'string' &&
          emailValidator(value)
        ) {
          setValidationError({ ...validationError, email: false });
        }
        break;
      case 'password':
        if (
          validationError.password &&
          passwordValidation &&
          passwordValidation.hasDigit &&
          passwordValidation.hasLowerCase &&
          passwordValidation.hasUpperCase &&
          passwordValidation.hasSpecialCharacter
        ) {
          setValidationError({ ...validationError, password: false });
        }
    }
    setDto({ ...dto, [type]: value });
  };

  /* snackbar close handler */
  const handleSnackbarClose = (type: 'error' | 'success') => {
    setSubmissionResponse({ ...submissionResponse, [type]: false });
  };

  return (
    <Box
      height="100vh"
      display="flex"
      justifyContent="center"
      alignItems="center"
    >
      <Snackbar
        open={submissionResponse.error}
        autoHideDuration={3000}
        onClose={() => handleSnackbarClose('error')}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      >
        <Alert variant="filled" severity="error">
          Error Creating User
        </Alert>
      </Snackbar>
      <Snackbar
        open={submissionResponse.success}
        autoHideDuration={3000}
        onClose={() => handleSnackbarClose('success')}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      >
        <Alert variant="filled" severity="success">
          User Created Successfully
        </Alert>
      </Snackbar>
      <RegisterComponent
        dto={dto}
        loading={submissionResponse.loading}
        validationError={validationError}
        handleClick={handleRegister}
        handleDtoChange={handleDtoChangeRegister}
      />
    </Box>
  );
};

export default RegisterPage;
