import { VisibilityOffOutlined, VisibilityOutlined } from '@mui/icons-material';
import { LoadingButton } from '@mui/lab';
import {
  Button,
  Card,
  CardContent,
  CardHeader,
  CircularProgress,
  Container,
  Divider,
  Unstable_Grid2 as Grid,
  IconButton,
  InputAdornment,
  Link,
  TextField,
  Typography,
} from '@mui/material';
import { FC, useState } from 'react';
import { Link as RouterLink, useNavigate } from 'react-router-dom';
import { ISignInProps } from '../types/ISignInProps';

const SignIn: FC<ISignInProps> = ({
  dto,
  validationError,
  loading,
  handleDtoChange,
  handleClick,
}) => {
  const [isPasswordVisible, setIsPasswordVisible] = useState(false);
  const navigate = useNavigate();
  return (
    <Container maxWidth="md">
      <Card variant="outlined" sx={{ p: 4 }}>
        <CardHeader
          title="Login"
          titleTypographyProps={{ variant: 'h4', component: 'span' }}
        />
        <CardContent component={Grid} container spacing={3}>
          <Grid width="100%">
            <TextField
              id="signin-email"
              name="email"
              label="Email"
              type="email"
              value={dto.email}
              error={validationError.email}
              helperText={validationError.email ? 'Should be an email' : null}
              required
              fullWidth
              onChange={(event) => {
                handleDtoChange('email', event.target.value);
              }}
            />
          </Grid>
          <Grid width="100%">
            <TextField
              id="sigin-password"
              name="password"
              label="Password"
              type={isPasswordVisible ? 'text' : 'password'}
              value={dto.password}
              required
              fullWidth
              onChange={(event) => {
                handleDtoChange('password', event.target.value);
              }}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton
                      onClick={() => {
                        setIsPasswordVisible(!isPasswordVisible);
                      }}
                    >
                      {!isPasswordVisible ? (
                        <VisibilityOffOutlined />
                      ) : (
                        <VisibilityOutlined />
                      )}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
            />
          </Grid>
          <Grid
            width="100%"
            display="flex"
            justifyContent="space-between"
            alignItems="end"
            marginBottom={4}
          >
            <LoadingButton
              size="large"
              variant="outlined"
              loading={loading}
              loadingIndicator={
                <CircularProgress
                  role="progressbar"
                  color="primary"
                  size={20}
                />
              }
              onClick={(event) => {
                event.preventDefault();
                handleClick();
              }}
            >
              Login
            </LoadingButton>
            <Link
              component={RouterLink}
              variant="body1"
              underline="hover"
              to="#"
            >
              Forgot Password?
            </Link>
          </Grid>
          <Grid width="100%">
            <Divider textAlign="center">
              <Typography>Don't have an account?</Typography>
            </Divider>
          </Grid>
          <Grid width="100%" display="flex" justifyContent="center">
            <Button
              size="large"
              variant="outlined"
              color="secondary"
              onClick={() => {
                navigate('/register');
              }}
            >
              Register
            </Button>
          </Grid>
        </CardContent>
      </Card>
    </Container>
  );
};

export default SignIn;
