import { AccountCircle, PhotoCamera } from '@mui/icons-material';
import {
  Avatar,
  Box,
  ButtonBase,
  Stack,
  Typography,
  styled,
} from '@mui/material';
import { ChangeEventHandler, FC, useState } from 'react';

const VisuallyHiddenInput = styled('input')({
  clip: 'rect(0 0 0 0)',
  clipPath: 'inset(50%)',
  height: 1,
  overflow: 'hidden',
  position: 'absolute',
  bottom: 0,
  left: 0,
  whiteSpace: 'nowrap',
  width: 1,
});

const Wrapper = styled(Box)({
  borderRadius: '50%',
  overflow: 'hidden',
});

const Overlay = styled(Box)({
  position: 'absolute',
  display: 'flex',
  flexWrap: 'wrap',
  top: 0,
  left: 0,
  width: '100%',
  height: '100%',
  background: 'rgb(0,0,0,0.5)',
  justifyContent: 'center',
  alignItems: 'center',
});

const ImageInput: FC<{
  id: string;
  src?: string;
  onChange: ChangeEventHandler<HTMLInputElement>;
}> = ({ id, src, onChange }) => {
  const [hovered, setHovered] = useState(false);
  return (
    <Wrapper
      id={id}
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => setHovered(false)}
    >
      <ButtonBase component="label" role={undefined} tabIndex={-1}>
        <Avatar src={src} sx={{ display: 'block', height: 170, width: 170 }}>
          {!src ? <AccountCircle sx={{ height: 170, width: 170 }} /> : null}
        </Avatar>
        <VisuallyHiddenInput
          type="file"
          accept="image/jpg, image/jpeg, image/png"
          onChange={onChange}
        />
        {hovered ? (
          <Overlay>
            <Stack spacing={1} display="flex" alignItems="center">
              <PhotoCamera color="action" />
              <Typography
                variant="caption"
                sx={{ color: (theme) => theme.palette.text.primary }}
              >
                Select a photo
              </Typography>
            </Stack>
          </Overlay>
        ) : null}
      </ButtonBase>
    </Wrapper>
  );
};

export default ImageInput;
